import React from 'react';
import { Grid, useBreakpoint } from '@jutro/layout';

// dynamic configuration for row with cells. Wrth to move to layout package?
export const AdjustableRow = props => {
    const { breakpointProps } = useBreakpoint(props, true);
    return <Grid columns={breakpointProps.spec}>{props.children}</Grid>;
};

const fullWidthConfig = { spec: ['1fr'] };

AdjustableRow.displayName = 'AdjustableRow';
AdjustableRow.defaultProps = {
    phone: fullWidthConfig,
    phoneWide: fullWidthConfig,
};
